import { useEffect, useState } from "react";
import { FormattedMessage, injectIntl } from "react-intl";

import "react-phone-number-input/style.css";

import { isObjEmpty, scrollToTop } from "@utils";
import { yupResolver } from "@hookform/resolvers/yup";
import classnames from "classnames";
import { useDispatch, useSelector } from "react-redux";
import { useHistory, useParams } from "react-router-dom";
import {
  Button,
  Col,
  Form,
  FormGroup,
  Input,
  Label,
  Media,
  Row,
} from "reactstrap";
import validateOptions from "@constants/validate";
import * as yup from "yup";
import { Controller, useForm } from "react-hook-form";

import Select from "react-select";
import {} from "react-scroll-up";
import { toast } from "react-toastify";

import { updateTimeTable, getProductId, getSectionId } from "../store/action";

const CourseTimeTableTab = ({ selected, intl }) => {
  const { id } = useParams();
  const store = useSelector((state) => state.timeTable);
  const product = useSelector((state) => state?.products?.data);
  const section = useSelector((state) => state?.terms?.data);
  const instructors = useSelector((state) => state?.instructors?.allData);
  const history = useHistory();
  const lang = useSelector((state) => state.common.language);
  const dispatch = useDispatch();
  const [changeSelect, setChangeSelect] = useState();
  const [instructorSeclet, setInstructorSeclet] = useState(null);

  const [status, setStatus] = useState(null);
  const [disable, setDisable] = useState(false);
  const [timeTableData, setTimeTableData] = useState(null);

  const [changcateProduct, setChangcateProduct] = useState();
  const optionProduct = {
    value: store?.productDetail?.id,
    label: store?.productDetail?.product_names[0]?.name,
  };
  const [changeSelectProduct, setChangeSelectProduct] =
    useState(changcateProduct);

  const changeCateProduct = (e) => {
    setChangeSelectProduct(e);
  };
  useEffect(() => {
    if (store) {
      setChangcateProduct(optionProduct);
    }
  }, [store]);
  useEffect(() => {
    if (changcateProduct) {
      setChangeSelectProduct(changcateProduct);
    }
  }, [changcateProduct]);

  const [changcateSection, setChangcateSection] = useState();
  const optionSection = {
    value: store?.sectionDetail?.id,
    label: store?.sectionDetail?.title,
  };

  const [changeSelectSection, setChangeSelectSection] =
    useState(changcateSection);

  const changeCateSection = (e) => {
    setChangeSelectSection(e);
  };
  useEffect(() => {
    if (store) {
      setChangcateSection(optionSection);
    }
  }, [store]);
  useEffect(() => {
    if (changcateSection) {
      setChangeSelectSection(changcateSection);
    }
  }, [changcateSection]);

  useEffect(() => {
    if (store?.err?.statusCode) {
      setDisable(false);
    }
  }, [store?.err]);

  useEffect(() => {
    dispatch(
      getProductId({
        id: selected?.productId,
        lang,
      })
    );
  }, [dispatch, selected?.productId]);

  useEffect(() => {
    dispatch(
      getSectionId({
        id: selected?.curriculumSectionId,
      })
    );
  }, [dispatch, selected?.curriculumSectionId]);

  useEffect(() => {
    if (
      selected !== null ||
      (selected !== null &&
        timeTableData !== null &&
        selected.id !== timeTableData.id)
    ) {
      setTimeTableData(selected);
      setStatus(selected?.status);
      setValue("status", selected?.status);
      setValue("productId", selected?.productId);
      setValue("curriculumSectionId", selected?.curriculumSectionId);
      setValue("instructorId", selected?.instructorId);
      setInstructorSeclet({
        value: selected?.instructor?.id || 0,
        label:
          selected?.instructor?.first_name +
            " " +
            selected?.instructor?.last_name || "",
      });
    }
  }, [selected, timeTableData]);

  const cate = yup.object({
    productId: yup
      .number()
      .required(<FormattedMessage id="The course field is required" />),
    curriculumSectionId: yup
      .number()
      .required(<FormattedMessage id="The term field is required" />),
    time: yup
      .string()
      .required(<FormattedMessage id="The time study field is required" />),
    date: yup
      .string()
      .required(<FormattedMessage id="The school day field is required" />),
    class: yup
      .string()
      .required(<FormattedMessage id="The class field is required" />),
    room: yup
      .string()
      .required(<FormattedMessage id="The room field is required" />),
    instructorId: yup
      .number()
      .required(<FormattedMessage id="The instructors field is required" />),
  });

  const {
    register,
    formState: { errors },
    handleSubmit,
    control,
    setError,
    getValues,
    setValue,
  } = useForm({
    resolver: yupResolver(cate),
    mode: "all",
  });

  const onSubmit = async (values) => {
    // if (isObjEmpty(errors)) {
    setDisable(true);
    dispatch(
      updateTimeTable({
        id: parseInt(id),
        data: {
          productId: values?.productId,
          curriculumSectionId: values?.curriculumSectionId,
          date: values?.date,
          class: values?.class,
          room: values?.room,
          link: values?.link,
          instructorId: values?.instructorId,
          time: values?.time,
          note: values?.note,
          status: status,
          lang: lang,
        },
      })
    );
    // }
  };

  useEffect(() => {
    if (store.status === 200) {
      scrollToTop();
      history.push("/apps/course/time-table/list");
    }
  }, [store.status, history]);

  return (
    <Row>
      <Col sm="12">
        <Media className="mb-2">
          <Media className="mt-50" body>
            <h4>{selected.name} </h4>
          </Media>
        </Media>
      </Col>
      <Col sm="12">
        <Form onSubmit={handleSubmit(onSubmit)}>
          <FormGroup>
            <Label>
              <FormattedMessage id="Course" />
              <span className="text-danger"> * </span>
            </Label>
            <Controller
              control={control}
              name="productId"
              render={({ field }) => {
                return (
                  <Select
                    id="productId"
                    innerRef={register}
                    name="productId"
                    placeholder={""}
                    value={changeSelectProduct}
                    className={classnames(
                      "react-select",
                      !changeSelectProduct
                        ? {
                            "is-invalid": errors["productId"],
                          }
                        : ""
                    )}
                    options={product?.map((item, index) => {
                      return {
                        value: item?.id,
                        label: item?.product_names[0]?.name || "",
                      };
                    })}
                    classNamePrefix="select"
                    {...field}
                    onChange={(e) => {
                      setError("productId", "");
                      setValue("productId", e?.value);
                      changeCateProduct(e);
                    }}
                  />
                );
              }}
            ></Controller>
            <small className="text-danger">
              {errors?.productId && errors.productId.message}
            </small>
          </FormGroup>

          <FormGroup>
            <Label>
              <FormattedMessage id="term" />
              <span className="text-danger"> * </span>
            </Label>
            <Controller
              control={control}
              name="curriculumSectionId"
              render={({ field }) => {
                return (
                  <Select
                    id="curriculumSectionId"
                    innerRef={register}
                    name="curriculumSectionId"
                    placeholder={""}
                    value={changeSelectSection}
                    className={classnames(
                      "react-select",
                      !changeSelectSection
                        ? {
                            "is-invalid": errors["curriculumSectionId"],
                          }
                        : ""
                    )}
                    options={section?.map((item, index) => {
                      return {
                        value: item?.id,
                        label: item?.title || "",
                      };
                    })}
                    classNamePrefix="select"
                    {...field}
                    onChange={(e) => {
                      setError("curriculumSectionId", "");
                      setValue("curriculumSectionId", e?.value);
                      changeCateSection(e);
                    }}
                  />
                );
              }}
            ></Controller>
            <small className="text-danger">
              {errors?.productId && errors.productId.message}
            </small>
          </FormGroup>

          <FormGroup>
            <Label for="time">
              <FormattedMessage id="time" />{" "}
              <span className="text-danger">*</span>
            </Label>
            <Input
              name="time"
              id="time"
              type="time"
              placeholder=""
              innerRef={register}
              onBlur={() => {
                let time = document.getElementById("time");
                if (time && time.value) {
                  time.value = time.value.trim();
                }
              }}
              className={classnames({ "is-invalid": errors["time"] })}
              defaultValue={timeTableData && timeTableData.time}
            />
            <small className="text-danger">
              {errors?.time && errors.time.message}
            </small>
            {errors?.time?.type == "validate" && (
              <small className="text-danger">
                <FormattedMessage id="Invalid name" />
              </small>
            )}
          </FormGroup>

          <FormGroup>
            <Label for="date">
              <FormattedMessage id="school day" />{" "}
              <span className="text-danger">*</span>
            </Label>
            <Input
              name="date"
              id="date"
              type="date"
              placeholder=""
              innerRef={register}
              onBlur={() => {
                let date = document.getElementById("date");
                if (date && date.value) {
                  date.value = date.value.trim();
                }
              }}
              className={classnames({ "is-invalid": errors["date"] })}
              defaultValue={timeTableData && timeTableData.date}
            />
            <small className="text-danger">
              {errors?.date && errors.date.message}
            </small>
            {errors?.date?.type == "validate" && (
              <small className="text-danger">
                <FormattedMessage id="Invalid name" />
              </small>
            )}
          </FormGroup>

          <FormGroup>
            <Label for="class">
              <FormattedMessage id="class" />{" "}
              <span className="text-danger">*</span>
            </Label>
            <Input
              name="class"
              id="class"
              type="class"
              placeholder=""
              innerRef={register}
              onBlur={() => {
                let className = document.getElementById("class");
                if (className && className.value) {
                  className.value = className.value.trim();
                }
              }}
              className={classnames({ "is-invalid": errors["class"] })}
              defaultValue={timeTableData && timeTableData.class}
            />
            <small className="text-danger">
              {errors?.class && errors.class.message}
            </small>
            {errors?.class?.type == "validate" && (
              <small className="text-danger">
                <FormattedMessage id="Invalid name" />
              </small>
            )}
          </FormGroup>

          <FormGroup>
            <Label for="room">
              <FormattedMessage id="room" /> <span class="text-danger">*</span>
            </Label>
            <Input
              name="room"
              id="room"
              type="room"
              placeholder=""
              innerRef={register}
              onBlur={() => {
                let room = document.getElementById("room");
                if (room && room.value) {
                  room.value = room.value.trim();
                }
              }}
              className={classnames({ "is-invalid": errors["room"] })}
              defaultValue={timeTableData && timeTableData.room}
            />
            <small className="text-danger">
              {errors?.room && errors.room.message}
            </small>
            {errors?.room?.type == "validate" && (
              <small className="text-danger">
                <FormattedMessage id="Invalid name" />
              </small>
            )}
          </FormGroup>

          {/* <FormGroup>
            <Label for="lecturers">
              <FormattedMessage id="Instructors" />{" "}
              <span class="text-danger">*</span>
            </Label>
            <Input
              name="lecturers"
              id="lecturers"
              placeholder=""
              innerRef={register}
              onBlur={() => {
                let lecturers = document.getElementById("lecturers");
                if (lecturers && lecturers.value) {
                  lecturers.value = lecturers.value.trim();
                }
              }}
              className={classnames({ "is-invalid": errors["lecturers"] })}
              defaultValue={timeTableData && timeTableData.lecturers}
            />
            <small className="text-danger">
              {errors?.lecturers && errors.lecturers.message}
            </small>
            {errors?.lecturers?.type == "validate" && (
              <small className="text-danger">
                <FormattedMessage id="Invalid name" />
              </small>
            )}
          </FormGroup> */}

          <FormGroup>
            <Label>
              <FormattedMessage id="instructors" />
              <span className="text-danger"> * </span>
            </Label>
            {instructors?.length > 0 && instructorSeclet && (
              <Controller
                control={control}
                name="instructorId"
                render={({ field }) => {
                  return (
                    <Select
                      id="instructorId"
                      innerRef={register}
                      name="instructorId"
                      placeholder={<FormattedMessage id="Select" />}
                      defaultValue={instructorSeclet}
                      className={classnames(
                        "react-select",
                        !changeSelect
                          ? {
                              "is-invalid": errors["instructorId"],
                            }
                          : ""
                      )}
                      options={instructors?.map((item, index) => {
                        return {
                          value: item?.id && item?.id,
                          label: item.first_name + " " + item.last_name || "",
                        };
                      })}
                      classNamePrefix="select"
                      {...field}
                      onChange={(e) => {
                        setError("instructorId", "");
                        setValue("instructorId", e?.value);
                        setChangeSelect(e?.value);
                      }}
                    />
                  );
                }}
              ></Controller>
            )}
            <small className="text-danger">
              {errors?.instructorId && errors.instructorId.message}
            </small>
          </FormGroup>

          <FormGroup>
            <Label for="link">
              <FormattedMessage id="Add link" />
            </Label>

            <Input
              name="link"
              id="link"
              placeholder=""
              innerRef={register}
              defaultValue={timeTableData && timeTableData.link}
            />
          </FormGroup>

          <FormGroup>
            <Label for="note">
              <FormattedMessage id="Note" />
            </Label>
            <Input
              name="note"
              id="note"
              placeholder=""
              innerRef={register}
              defaultValue={timeTableData && timeTableData.note}
            />
          </FormGroup>

          <FormGroup>
            <Label for="status">
              <FormattedMessage id="Status" />
            </Label>

            <Input
              type="select"
              name="status"
              value={status}
              onChange={(e) => setStatus(e.target.value)}
              id="status"
              innerRef={register({ required: true })}
            >
              <option value={1} style={{ padding: "10px 0px" }}>
                {intl.formatMessage({ id: "Active" })}
              </option>

              <option value={0}>{intl.formatMessage({ id: "Blocked" })}</option>
            </Input>
          </FormGroup>

          <div style={{ textAlign: "end", marginTop: "145px" }}>
            <Button
              type="submit"
              className="mr-1"
              color="primary"
              disabled={disable}
            >
              <FormattedMessage id="update" />
            </Button>
            <Button
              type="reset"
              color="secondary"
              outline
              onClick={() => history.goBack()}
            >
              <FormattedMessage id="Cancel" />
            </Button>
          </div>
        </Form>
      </Col>
    </Row>
  );
};
export default injectIntl(CourseTimeTableTab);
