import { useEffect, useState } from "react";
import { AlignJustify, Calendar } from "react-feather";
import { FormattedMessage } from "react-intl";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import {
  Alert,
  Card,
  CardBody,
  Col,
  Nav,
  NavItem,
  NavLink,
  Row,
  TabContent,
  TabPane,
} from "reactstrap";
import CourseTimeTableTab from "./CourseTimeTable";
import { getById } from "../store/action";
import { toast } from "react-toastify";
import { isObjEmpty, scrollToTop } from "@utils";
import { getData as getDataProduct } from "../../../product/store/action";
import { getData as getDataSection } from "../../../product/term/store/action";
import { getData as getDataInstructors } from "../../../instructors/store/action";

const CourseTimeTableEdit = () => {
  const [activeTab, setActiveTab] = useState("1"),
    store = useSelector((state) => state.timeTable),
    dispatch = useDispatch(),
    { id } = useParams();
  const lang = useSelector((state) => state.common.language);

  const toggle = (tab) => setActiveTab(tab);

  useEffect(() => {
    dispatch(
      getDataProduct({
        filter: {
          status: 1,
          product_type: "0",
          lang: lang,
        },
        // skip: 0,
        // limit: 20,
        order: [
          {
            key: "status",
            value: "DESC",
          },
        ],
      })
    );
    dispatch(
      getDataSection({
        filter: {
          status: 1,
          lang: lang,
        },
        // skip: 0,
        // limit: 20,
        order: [
          {
            key: "id",
            value: "asc",
          },
        ],
      })
    );
    dispatch(
      getDataInstructors({
        filter: {
          lang: lang,
          // status: 1,
        },
        order: [
          {
            key: "id",
            value: "asc",
          },
        ],
      })
    );
  }, []);

  useEffect(() => {
    dispatch(getById(parseInt(id)));
  }, [dispatch]);

  return store.selected !== null && store.selected !== undefined ? (
    <Row className="app-user-edit">
      <Col sm="12">
        <Card>
          <CardBody className="pt-2">
            <Nav pills>
              <NavItem>
                <NavLink active={activeTab === "1"} onClick={() => toggle("1")}>
                  <Calendar size={14} />
                  <span className="align-middle d-none d-sm-block">
                    <FormattedMessage id="Timetable" />
                  </span>
                </NavLink>
              </NavItem>
            </Nav>
            <TabContent activeTab={activeTab}>
              <TabPane tabId="1">
                <CourseTimeTableTab selected={store.selected} store={store} />
              </TabPane>
            </TabContent>
          </CardBody>
        </Card>
      </Col>
    </Row>
  ) : (
    <Alert color="danger">
      <h4 className="alert-heading">Not found</h4>
    </Alert>
  );
};
export default CourseTimeTableEdit;
